}

*, :after, :before {
  box-sizing: inherit;
  border: 0;
  margin: 0;
}

a, a:visited {
  color: inherit;
  text-decoration: none;
}

html {
  font-size: 62.5%;
}

body {
  color: #777;
  box-sizing: border-box;
  padding: 3rem;
  font-family: Lato, sans-serif;
  font-weight: 400;
  line-height: 1.7;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

@keyframes letterSpacingIn {
  0% {
    opacity: 0;
    letter-spacing: 6rem;
  }

  50% {
    opacity: .2;
  }

  100% {
    opacity: 1;
  }
}

@keyframes letterSpacingIn--subtle {
  0% {
    opacity: 0;
    letter-spacing: 2.5rem;
  }

  50% {
    opacity: .2;
  }

  100% {
    opacity: 1;
  }
}

@keyframes moveUp {
  0% {
    opacity: 0;
    transform: translateY(.4rem);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.heading {
  text-align: center;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.heading__hero {
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 2rem;
}

.heading__hero--main {
  letter-spacing: 3.5rem;
  animation-name: letterSpacingIn;
  animation-duration: 1.25s;
  animation-timing-function: .8, .8, .9, .9;
  font-size: 6rem;
  display: block;
}

.heading__hero--sub {
  letter-spacing: 1.7rem;
  animation-name: letterSpacingIn--subtle;
  animation-duration: 1.25s;
  animation-timing-function: .8, .8, .9, .9;
  font-size: 2rem;
  font-weight: 700;
  display: block;
}

.heading__secondary {
  text-transform: uppercase;
  background-image: linear-gradient(90deg, #6fd2d5, #5593c5);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-background-clip: text;
  color: #0000;
  letter-spacing: normal;
  font-size: 3.5rem;
  font-weight: 700;
  transition: letter-spacing .2s cubic-bezier(.8, .8, .9, .9);
  display: inline-block;
}

.heading__secondary:hover {
  letter-spacing: .05rem;
}

.heading__tertiary {
  text-transform: uppercase;
  font-size: 1.6rem;
  font-weight: 700;
}

.paragraph {
  font-size: 1.6rem;
}

.section {
  padding: 20rem 0;
}

.section__about {
  background-color: #f6f6f6;
  margin-top: -11rem;
}

.section__features {
  background-blend-mode: overlay;
  background-image: linear-gradient(to top right, #6fd5bdb3, #5596c5b3), url("nat-4.edc3d12b.jpg");
  background-position: top;
  background-size: cover;
  margin-top: -11rem;
  transform: skewY(-3deg);
}

.section__features > * {
  transform: skewY(3deg);
}

.section__tours {
  background-color: #f6f6f6;
  margin-top: -11rem;
  padding-bottom: 8rem;
}

.section__testimonials {
  padding-top: 8rem;
  padding-bottom: 8rem;
  position: relative;
}

.section__booking {
  background-image: linear-gradient(to top right, #6fd5bdb3, #5596c5b3);
  padding-top: 8rem;
  padding-bottom: 8rem;
}

.btn, .btn:link, .btn:visited {
  cursor: pointer;
  min-width: 15rem;
  text-transform: uppercase;
  color: inherit;
  text-align: center;
  background-color: #fff;
  border-radius: 100vw;
  padding: 1.5rem 2rem;
  font-size: 1.6rem;
  text-decoration: none;
  transition: transform .15s cubic-bezier(.2, .2, .1, .2), box-shadow 50ms cubic-bezier(.2, .2, .1, .2), color .15s cubic-bezier(.2, .2, .1, .2), background-color .15s cubic-bezier(.2, .2, .1, .2);
  box-shadow: .2px .9rem .9rem #0003;
}

.btn:hover, .btn:focus, .btn:link:hover, .btn:link:focus, .btn:visited:hover, .btn:visited:focus {
  box-shadow: 0 .1rem .4rem #00000080;
}

.btn__hero {
  display: inline-block;
}

.btn__hero:hover {
  transform: translateY(-.4rem);
}

.btn__hero:active {
  transform: translateY(-.4rem);
  box-shadow: 0 .1rem .4rem #00000080;
}

.btn__about {
  color: #6fd2d5;
  background-color: #fff;
  border-bottom: 2px solid #6fd2d5;
  padding: 1rem 2rem;
  font-size: 1.6rem;
  text-decoration: none;
  transition: background-color .15s cubic-bezier(.8, .8, .9, .9), color .15s cubic-bezier(.8, .8, .9, .9);
  display: inline-block;
}

.btn__about:hover, .btn__about:focus {
  color: #fff;
  background-color: #6fd2d5;
}

.btn__card:hover, .btn__card:focus {
  box-shadow: 0 .1rem .4rem #00000080;
}

.btn__tours--all {
  display: inline-block;
}

.btn__tours--all:hover, .btn__tours--all:focus {
  box-shadow: 0 .1rem .4rem #00000080;
}

.composition {
  position: relative;
}

.composition__photo {
  width: 55%;
  z-index: 1;
  border-radius: 2px;
  transition: transform .15s cubic-bezier(.8, .8, .9, .9);
  display: block;
  position: absolute;
  scale: 1;
  box-shadow: 0 .1rem .4rem #00000080;
}

.composition__photo--p1 {
  top: 2rem;
  left: 0;
}

.composition__photo--p2 {
  top: 5em;
  right: 0;
}

.composition__photo--p3 {
  top: 10rem;
  left: 20%;
}

.composition__photo:hover {
  z-index: 2;
  transform: scale(1.1);
}

.composition:hover .composition__photo:not(:hover) {
  transform: scale(.95);
}

.feature {
  text-align: center;
  min-height: 25rem;
  background-color: #fffc;
  border-radius: 2px;
  padding: 2.5rem;
  font-size: 1.6rem;
  transition: transform, all .15s cubic-bezier(.8, .8, .9, .9), scale .15s cubic-bezier(.8, .8, .9, .9);
  transform: translate(0)scale3d(1, 1, 1);
  box-shadow: 0 .1rem .4rem #00000080;
}

.feature:hover {
  transform: translateY(-1rem)scale3d(1.03, 1.03, 1);
}

.feature__icon:before {
  color: #0000;
  background-image: linear-gradient(to top right, #6fd5bdb3, #5596c5b3);
  -webkit-background-clip: text;
  background-clip: text;
  margin-bottom: 1rem;
  font-size: 6rem;
  display: inline-block;
}

.tours__card {
  perspective: 150rem;
}

.tours__card-side {
  min-height: 50rem;
  width: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 6px;
  transition: transform .15s cubic-bezier(.8, .8, .9, .9);
  position: relative;
  top: 0;
  left: 0;
  overflow: hidden;
  box-shadow: .2px .9rem .9rem #0003;
}

.tours__card-side--front {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  position: absolute;
}

.tours__card-side--back {
  background-color: #90ee90;
  padding: 1rem;
  transform: rotateY(180deg);
}

.tours__card-side--back-1 {
  background-image: linear-gradient(#ffa54be6, #ea9108e6);
}

.tours__card-side--back-2 {
  background-image: linear-gradient(#48ce68e6, #22c02ee6);
}

.tours__card-side--back-3 {
  background-image: linear-gradient(#4eb4efe6, #197cdce6);
}

.tours__card:hover .tours__card-side--front {
  transform: rotateY(-180deg);
}

.tours__card:hover .tours__card-side--back {
  transform: rotateY(0);
}

.tours__card-image {
  height: 23rem;
  background-blend-mode: soft-light;
  -webkit-clip-path: polygon(0 0, 0 100%, 100% 85%, 100% 0);
  clip-path: polygon(0 0, 0 100%, 100% 85%, 100% 0);
  background-size: cover;
  position: relative;
}

.tours__card-image--1 {
  background-image: linear-gradient(#ffa54be6, #ea9108e6), url("nat-5.107d9ca4.jpg");
}

.tours__card-image--2 {
  background-image: linear-gradient(#48ce68e6, #22c02ee6), url("nat-6.7dbfbeae.jpg");
}

.tours__card-image--3 {
  background-image: linear-gradient(#4eb4efe6, #197cdce6), url("nat-7.076de6cc.jpg");
}

.tours__card-heading {
  color: #0000;
  text-align: center;
  text-transform: uppercase;
  -webkit-background-clip: text;
  background-clip: text;
  padding: 1rem;
  font-size: 2rem;
  font-weight: 700;
}

.tours__card-heading--1 {
  background-image: linear-gradient(#ffa54be6, #ea9108e6);
}

.tours__card-heading--2 {
  background-image: linear-gradient(#48ce68e6, #22c02ee6);
}

.tours__card-heading--3 {
  background-image: linear-gradient(#4eb4efe6, #197cdce6);
}

.tours__card-details {
  width: 80%;
  color: #222;
  text-align: center;
  margin: 0 auto;
  font-size: 1.6rem;
}

.tours__card-details li {
  padding: 1rem;
}

.tours__card-details li + li {
  border-top: 1px solid #dcdcdc;
}

.tours__card-pricebox {
  text-align: center;
  font-size: 6rem;
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.tours__card-price {
  color: #fff;
  font-weight: 700;
}

.tours__card-price span {
  font-size: 3rem;
  font-weight: 300;
  display: block;
}

.testimonials {
  width: 75%;
  margin: 0 auto;
}

.testimonials__item {
  background-color: #fff;
  border-radius: 2px;
  padding: 4rem 4rem 4rem 6rem;
  font-size: 1.6rem;
  transform: skewX(-12deg);
  box-shadow: 0 2rem 4rem #0003;
}

.testimonials__item + .testimonials__item {
  margin-top: 8rem;
}

.testimonials__item > * {
  transform: skewX(12deg);
}

.testimonials__text {
  color: #222;
}

.testimonials__item:hover .testimonials__image-caption {
  opacity: 1;
  transform: translate(-50%, -50%);
}

.testimonials__item:hover .testimonials__image {
  filter: blur(.1rem) brightness(.7);
  transform: scale(1);
}

.testimonials__image-caption {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  opacity: 0;
  border-radius: 2px;
  font-size: 1.6rem;
  transition: transform .25s cubic-bezier(.2, .2, .1, .2), opacity .25s cubic-bezier(.2, .2, .1, .2);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
}

.testimonials__image-wrapper {
  height: 15rem;
  width: 15rem;
  float: left;
  shape-outside: circle(50% at 50% 50%);
  -webkit-shape-outside: circle(50% at 50% 50%);
  -webkit-clip-path: circle(50%);
  clip-path: circle(50%);
  position: relative;
  translate: -2rem;
}

.testimonials__image {
  height: 100%;
  transition: transform .25s cubic-bezier(.2, .2, .1, .2), filter .25s cubic-bezier(.2, .2, .1, .2);
  position: absolute;
  left: -25%;
  transform: scale(1.4);
}

.bg__video {
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: .2;
  filter: blur(.1rem) brightness(.9);
  position: absolute;
  top: 0;
  left: 0;
}

.bg__video--content {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.booking {
  height: 50rem;
  background-image: linear-gradient(105deg, #fffc 0% 55.2%, #0000 55.5%), url("nat-10.1a680fb3.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 2px;
  padding: 4rem;
  position: relative;
  box-shadow: .2px .9rem .9rem #0003;
}

.booking__form {
  width: 40%;
  color: #222;
  font-family: inherit;
  font-size: 1.6rem;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}

.booking__form--label {
  flex-flow: column;
  display: flex;
  position: relative;
}

.booking__form--label:not(:last-child) {
  margin-bottom: 2rem;
}

.booking__form--input-wrapper {
  position: relative;
}

.booking__form--input-wrapper + .booking__form--input-wrapper {
  margin-top: 4rem;
}

.booking__form-input {
  color: inherit;
  background-color: #ffffffb3;
  border: 2px solid #5593c5;
  border-radius: 6px;
  padding: 1rem 2rem;
}

.booking__form-input::placeholder {
  color: tomato;
}

.booking__form-input:focus + .booking__form-label-text, .booking__form-input:not(:placeholder-shown) + .booking__form-label-text {
  color: #222;
  transform: translate(1rem, -5rem);
}

.booking__form-input:focus {
  border: 2px solid #6fd2d5;
  outline: none;
  box-shadow: .2px .9rem .9rem #0003;
}

.booking__form-input:focus:invalid {
  border: 2px solid #fd9b30;
  outline: none;
  box-shadow: .2px .9rem .9rem #0003;
}

.booking__form-label-text {
  color: #adadad;
  transition: transform .15s cubic-bezier(.8, .8, .9, .9), color .15s cubic-bezier(.8, .8, .9, .9);
  position: absolute;
  top: 50%;
  left: 1rem;
  transform: translate(1rem, -50%);
}

.booking__form-radio-group {
  justify-content: center;
  display: flex;
}

.booking__form-radio {
  margin-top: 2rem;
}

.booking__form-radio-input {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.booking__form-radio-input:checked + .booking__form-radio-label > .booking__form-radio-button:after {
  opacity: 1;
}

.booking__form-radio-label {
  cursor: pointer;
  color: #222;
  margin-left: 5rem;
  position: relative;
}

.booking__form-radio-button {
  height: 2rem;
  width: 2rem;
  border: 2px solid #5593c5;
  border-radius: 50%;
  position: absolute;
  top: .1rem;
  left: -3rem;
}

.booking__form-radio-button:after {
  content: "";
  height: 1rem;
  width: 1rem;
  opacity: 0;
  background-color: #5593c5;
  border-radius: 50%;
  transition: opacity .1s ease-in;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.row {
  max-width: 114rem;
  margin: 0 auto;
}

.row:not(:last-child) {
  margin-bottom: 6rem;
}

.row:after {
  content: "";
  clear: both;
  display: table;
}

.row [class^="col"] {
  float: left;
}

.row [class^="col"]:not(:last-child) {
  margin-right: 6rem;
}

.row .col-1-of-2 {
  width: calc(50% - 3rem);
}

.row .col-1-of-3 {
  width: calc(33.3333% - 4rem);
}

.row .col-2-of-3 {
  width: calc(66.6667% - 2rem);
}

.row .col-1-of-4 {
  width: calc(25% - 4.5rem);
}

.row .col-2-of-4 {
  width: calc(50% - 3rem);
}

.row .col-3-of-4 {
  width: calc(75% - 1.5rem);
}

.header {
  height: 95vh;
  background-blend-mode: multiply;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);
  clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);
  background-image: linear-gradient(to top right, #6fd5bdb3, #5596c5b3), url("hero.d24b10d1.jpg");
  background-position: top;
  background-size: cover;
  position: relative;
}

.header__logo-wrapper {
  position: absolute;
  top: 3rem;
  left: 3rem;
}

.header__logo {
  height: 3.5rem;
}

.about__details {
  margin-bottom: 2rem;
  margin-left: 0;
  padding: 0;
  list-style: none;
}

.about__details li + li {
  margin-top: 3rem;
}

.footer {
  background-color: #424242;
  padding: 4rem 0;
  font-size: 1.4rem;
}

.footer__footer-logo {
  width: 15rem;
  margin: 0 auto;
  display: block;
}

.footer__categories-list {
  display: flex;
}

.footer__categories-item {
  cursor: pointer;
  color: #8f8f8f;
  padding: 1rem;
  transition: color .15s ease-in;
}

.footer__categories-item:hover, .footer__categories-item:focus {
  color: #fff;
}

.footer__categories-item + .footer__categories-item {
  margin-left: 1rem;
}

.footer__copyright {
  text-align: right;
  padding: 1rem;
}

.footer__copyright-link {
  color: #8f8f8f;
  font-weight: 700;
  transition: color .15s ease-in;
}

.footer__copyright-link:hover, .footer__copyright-link:focus {
  color: #fff;
}

.alt-navigation {
  position: relative;
}

.alt-navigation__button {
  cursor: pointer;
  width: 5rem;
  height: 5rem;
  z-index: 4;
  background-color: #fff;
  border-radius: 50%;
  position: fixed;
  top: 5rem;
  right: 5rem;
  box-shadow: .2px .9rem .9rem #0003;
}

.alt-navigation__checkbox {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.alt-navigation__backdrop {
  width: 6rem;
  height: 6rem;
  z-index: 2;
  background-image: radial-gradient(#6fd2d5, #5593c5);
  border-radius: 50%;
  transition: transform .15s cubic-bezier(.2, .2, .1, .2);
  position: fixed;
  top: 5rem;
  right: 5rem;
  transform: scale(0);
}

.alt-navigation__nav {
  height: 100vh;
  width: 100vw;
  z-index: 3;
  opacity: 0;
  pointer-events: none;
  transition: opacity .15s cubic-bezier(.2, .2, .1, .2);
  position: fixed;
  top: 0;
  right: 0;
}

.alt-navigation__nav-list {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.alt-navigation__nav-item {
  text-align: left;
  color: #fff;
}

.alt-navigation__nav-item + .alt-navigation__nav-item {
  margin-top: 2rem;
}

.alt-navigation__nav-link:link, .alt-navigation__nav-link:visited {
  text-transform: uppercase;
  background-image: linear-gradient(120deg, #0000 0% 50%, #fff 50%);
  background-size: 220%;
  padding: 1rem 3rem;
  font-size: 3rem;
  transition: background-position .15s ease-in-out, color .15s ease-in-out, transform .15s ease-in-out;
  display: block;
  transform: translateX(0);
}

.alt-navigation__nav-link:hover, .alt-navigation__nav-link:focus {
  color: #6fd2d5;
  background-position: 100%;
  transform: translateX(5rem);
}

.alt-navigation__button ~ .alt-navigation__checkbox:checked .alt-navigation__backdrop, .alt-navigation__checkbox:checked ~ .alt-navigation__backdrop {
  transform: scale(100);
}

.alt-navigation__checkbox:checked ~ .alt-navigation__nav {
  opacity: 1;
  pointer-events: all;
}

.alt-navigation__icon {
