.header {
  position: relative;
  height: 95vh;
  background-image: $default-gradient, url('../images/hero.jpg');
  background-blend-mode: multiply;
  background-size: cover;
  background-position: top;
  clip-path: polygon(0 0, 100% 0, 100% 85%, 0% 100%);

  &__logo-wrapper {
    position: absolute;
    left: 3rem;
    top: 3rem;
  }

  &__logo {
    height: 3.5rem;
  }
}
